.Personne ul{
    display: flex;
    position: absolute;
    flex-direction: column;
    height: 98%;
    background-color: #2C3E50;
    width: 15%;
    right: 0;
    font-weight: 90;
    list-style: none;
    justify-content: flex-start;
    margin-top: -10px;
    margin-bottom: 0px;
    padding: 10px;
}

.Personne li {
    display: flex;
    flex-direction: column;
    margin: 10px;
    text-align: center;
    font-size: 20px;
    color: white;
    border-radius: 18px;
    background-color: #396086;
    margin-bottom: 5px;
}

.Personne p {
    color: white;
}